export default {
	data() {
		return {
			style: '',
			loveNum: 0
		};
	},
	methods:{
		close(){
			this.$emit('close')
		},
		change() {
			this.style = this.style === '' ? 'color: red;' : '';
			this.loveNum = this.style === "color: red;" ? this.loveNum+1 : this.loveNum-1;
		}
	}
}
